import React from 'react';
import Aux from '../../hoc/Aux';
import cls from './Layout.css';

const layout =  (props) =>(
    <Aux>

    <div>Toolbar,SideDrawer,Backdrop</div>
    <main className={cls.Content}>
        {props.children}
    </main>
    </Aux>
);

export default layout;


